@import url('https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Mochiy+Pop+P+One&display=swap');

* {
    margin: 0;
    padding: 0;
}

#theTitle {
    font-family: 'Mochiy Pop P One', sans-serif;
}
h1, h3, p {
    font-family: 'Open Sans Condensed', sans-serif;
}

.App {
    height: 100%;
    width: 100%;
    background-color: #0F2C67;
}

.App-header {
    background-color: #F3950D;
    min-height: 35vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    text-shadow: 5px 1px 15px #CD1818, -5px -1px 15px #0F2C67;
    color:  #F3950D;
    border-radius: 0 0 85% 85% / 60%;
    z-index: 5;

    animation: scroll 150s linear infinite;
    background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url("https://images.unsplash.com/photo-1465146633011-14f8e0781093?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=3450&q=80"), #111111;
    perspective: 1000px;
    perspective-origin: 50% 50%;
}

.App-header > h4, .App-header > p {
    font-size: 25px;
    color: #CD1818;
    text-shadow: 2px 1px 10px #0F2C67;
}

.App-body {
    min-height: 150vh;
}


/* PRODUCT CLASSES */

#product-carousel {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}

.product-border {
    color: #CD1818;
    width: 350px;
    height: auto;
    min-height: 390px;
    margin-top: 30px;
    margin-left: 20px;
    word-wrap: break-word;
    text-align: center;
    font-size: +19px;
    background-color: #F3950D;
}

.product-border > p{
    text-shadow: 0px 0px 10px #F4E185;
    font-weight: bold;
}
.product-border > h1{
    text-shadow: 0px 0px 10px #F4E185;
    cursor: pointer;
}

.junkpics {
    width: 12vw;
    height: 25vh;
    min-width: 200px;
    min-height: 240px;
    filter:contrast(500%);
}

.productRatio {
    width: 100px;
    margin-bottom: 10px;
}

/* SHOPPING CART STUFF */

#thatOneCart {
    position: absolute;
    font-size: 30px;
    top: 20px;
    right: 20px;
    z-index: 50;
    cursor: pointer;
}

.shineCart {
    text-shadow: 0 0 5px #FFF, 0 0 10px #FFF, 0 0 15px #FFF, 0 0 20px #406882, 0 0 30px #406882, 0 0 40px #406882, 0 0 55px #406882, 0 0 75px #406882;
}

.darken-screen {
    position: fixed;
    top: 0px;
    right: 0px;
    width: 100%;
    height: 100%;
    background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
    z-index: 20;
}

.cart-container {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 700px;
    height: 800px;
    text-align: center;
    background-color: #1A374D;
    border-radius: 15px;
    box-shadow: 0px 0px 40px 20px #406882;
    z-index: 50;
}

.cart-container > h3 {
    font-size: 40px;
    color: white;
    text-shadow: 2px 1px 15px #406882;
}

.cart-products {
    position: relative;
    width: 600px;
    height: 400px;
    margin-top: 50px;
    margin-left:auto;
    margin-right:auto;
    border: 1px solid black;
    overflow: scroll;
    overflow-x: hidden;
    background-color: #406882;
}

.carted-placement{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.carted-border {
    position: relative;
    width: 90%;
    height: 120px;
    background-color: #1A374D;
    margin-top: 15px;
    margin-left:auto;
    margin-right:auto;
    display: flex;
    flex-direction: row;
    border-radius: 10px;
}

.carted-border > img {
    width: 100px;
    height: 100px;
    min-width: 40px;
    min-height: 100px;
    filter:contrast(500%);
    margin-top: auto;
    margin-bottom: auto;
    margin-left: 20px;
    margin-right: 20px;
}

.carted-info {
    width: 250px;
}

.carted-info > h3 {
    font-size: 30px;
    color: white;
    text-shadow: 2px 1px 15px #fff;
}
.carted-info > p {
    font-weight: bold;
    color: white;
}

.quantityShowing {
    margin: auto;
    font-size: 25px;
    color: #CD1818;
    text-shadow: 0px 0px 5px #fff, 0px 0px 10px #F4E185;
}

.cart-butt {
    width: 100%;
    margin-top: 15px;;
    display:flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}

.cart-buttons {
    width: 100px;
    height: 60px;
    margin-right: 15px;
    margin-left: 15px;
    background-color: #406882;
    border-radius: 4px;
    border-style: none;
    box-sizing: border-box;
    color: #fff;
    cursor: pointer;
    transition: all 0.3s ease 0s;
}

.cart-buttons:hover {
    background: #2a5377
}

/* USER INFORMATION FORM*/

.user-form > form > input[type="submit"] {
    width: 100px;
    height: 40px;
    margin-top: 10px;
    margin-right: 15px;
    margin-left: 15px;
    background-color: #222;
    border-radius: 4px;
    border-style: none;
    box-sizing: border-box;
    color: #fff;
    cursor: pointer;
    transition: all 0.3s ease 0s;
}

.user-form > form > input[type="submit"]:hover {
    background: #707070
}

/* CHECKOUT DESIGN*/

.checkout-container {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 900px;
    height: 500px;
    text-align: center;
    background-image: url(https://cdn.pixabay.com/photo/2015/05/05/09/42/paper-753508_1280.jpg);
    background-color: #1A374D;
    border-radius: 15px;
    box-shadow: 0px 0px 40px 20px #406882;
    z-index: 60;
}

/* ANIMATION */

@keyframes scroll {
    100%{
        background-position:0px -3000px;
    }
}